<template>

    <div class="loginwrapper">
    <div class="login">
        <div class="logininner"><input v-model.trim="token" type="text" name="AccessToken" id="" placeholder="Access Token"></div>
        <div class="logininner"><input type="submit" value="Sign in" v-on:click="login(token)"></div>
    </div>
    </div>
</template>



<script>
export default {
    data(){
        return {
            token:'',
        }
    },
    methods:{
        login:function(value){
            if(value){
                this.$axios.post('https://cnodejs.org/api/v1/accesstoken/',{
                    params:{
                        accesstoken:this.token
                    }               
                })
                .then(res=>{
                    if(res.data.sucess){
                        this.$router.push("/")
                    }
                })
                .catch(err=>{
                   alert('您输入的Access Token有误！')
                })
            }else{
                alert('请您输入的Access Token')
            }
        }
    }

}
</script>



<style scoped>
*{
    box-sizing: border-box;
}
.loginwrapper{

 margin-top:15%;

}


/* .login{
    width: 300px;
    padding: 70px 20px;
    background-color: rgb(68, 68, 68);;
} */


.login {
    
    width:260px;
  
  margin: auto;
  background-color:mediumslateblue;

}
.logininner{
    padding:20px;
 
}

input{
   text-align: center;
   width: 220px;
   height:36px;
   border-radius: 3px;
   outline: none;
 
}
</style>



